body {
  font-family: 'Segoe UI', sans-serif;
  background: #f4f6fb;
  color: #1f2937;
  margin: 0;
  padding: 0;
}

.quiz-container {
  background: #fff;
  border-radius: 20px;
  padding: 32px;
  box-shadow: 0 12px 24px rgba(0, 0, 0, 0.08);
  max-width: 900px;
  margin: 40px auto;
}

.question-title {
  font-size: 18px;
  font-weight: 600;
  margin-bottom: 12px;
}

.answer-button {
  width: 100%;
  padding: 14px 20px;
  border-radius: 12px;
  border: 2px solid #d1d5db;
  background-color: white;
  font-size: 16px;
  text-align: left;
  transition: all 0.2s ease;
  font-weight: 500;
}

.answer-button:hover {
  background-color: #e0f2f1;
  border-color: #10b981;
  cursor: pointer;
}

.answer-button.selected {
  background-color: #10b981;
  color: white;
  font-weight: 700;
  border-color: #047857;
  box-shadow: 0 4px 12px rgba(16, 185, 129, 0.4);
  transform: scale(1.02);
}

.time-left {
  color: #dc2626;
  font-weight: 600;
  font-size: 18px;
  margin-bottom: 20px;
}

.result-box {
  text-align: center;
  font-size: 20px;
  font-weight: bold;
  margin-top: 30px;
}
